import React from "react"
import Link from "./Link"

export function SidebarLink({ href, children }) {
  return (
    <li className="mt-2">
      <Link href={href}>
        <a className="text-primary-600 hover:text-primary-800 hover:underline">
          {children}
        </a>
      </Link>
    </li>
  )
}

export function SidebarLinkList({ children }) {
  return <ul className="my-4">{children}</ul>
}

export function SidebarHeading({ children }) {
  return <h3 className="mt-8 text-inherit">{children}</h3>
}

export default function SideBar({ children }) {
  return <section className="p-4 text-sm">{children}</section>
}
